.imageGallery {
    max-width: 100%;
    margin: auto;
    --slide-height: 19rem;
    --slide-spacing: 1rem;
    --slide-size: 100%;
}

.imageGallery__viewport {
    overflow: hidden;
}

.imageGallery__container {
    backface-visibility: hidden;
    display: flex;
    touch-action: pan-y;
    margin-left: calc(var(--slide-spacing) * -1);
}

.imageGallery__slide {
    flex: 0 0 var(--slide-size);
    min-width: 0;
    padding-left: var(--slide-spacing);
    height: 650px;
    margin-bottom: 25px;

    img {
        width: 100%;
        height: 100%;
        max-width: 100%;
        object-fit: cover;
    }
}

.imageGallery-thumbs {
    --thumbs-slide-spacing: 10px;
    --thumbs-slide-height: 180px;
    margin-top: var(--thumbs-slide-spacing);
}

.imageGallery-thumbs__viewport {
    overflow: hidden;
}

.imageGallery-thumbs__container {
    display: flex;
    flex-direction: row;
    margin-left: calc(var(--thumbs-slide-spacing) * -1);
}

.imageGallery-thumbs__slide {
    flex: 0 0 22%;
    min-width: 0;
    padding-left: var(--thumbs-slide-spacing);
    min-width: calc(640px / 3 - var(--thumbs-slide-spacing));
}

@media (min-width: 576px) {
    .imageGallery-thumbs__slide {
        flex: 0 0 15%;
    }
}

.imageGallery-thumbs__slide__image {
    img {
        width: 100%;
        height: 100%;
        max-width: 100%;
        object-fit: cover;
        aspect-ratio: 1 / 1;
    }
}

.imageGallery-thumbs__slide--selected .imageGallery-thumbs__slide__number {
    color: var(--text-body);
}

.imageGallery__controls {
    display: grid;
    grid-template-columns: auto 1fr;
    justify-content: space-between;
    gap: 1.2rem;
    margin-top: 1.8rem;
}

.imageGallery__buttons {
    display: flex;
    width: 100%;
    justify-content: center;
}

.imageGallery__button {
    -webkit-tap-highlight-color: rgba(var(--text-high-contrast-rgb-value), 0.5);
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
    touch-action: manipulation;
    display: inline-flex;
    text-decoration: none;
    cursor: pointer;
    border: 0;
    padding: 0;
    margin: 0;
    box-shadow: inset 0 0 0 0.2rem var(--detail-medium-contrast);
    width: 3.6rem;
    height: 3.6rem;
    z-index: 1;
    border-radius: 50%;
    color: var(--text-body);
    display: flex;
    align-items: center;
    justify-content: center;
}

.imageGallery__button:disabled {
    color: var(--detail-high-contrast);
}

.imageGallery__button__svg {
    width: 35%;
    height: 35%;
}